.refresh {
  font-family: 'poppins', sans-serif;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: small;
}

.main-front {
  height: 100vh;
  width: 100%;
  position: fixed;
  /* box-shadow: inset 0 4px 2px -2px rgb(47, 42, 42); */
  line-height: 1.6;
}

.title-ch {
  margin-top: 3px;
  height: 25px;
  font-family: 'poppins', sans-serif;
  font-weight: 600;
  color: aliceblue;
  text-shadow: 1px 1px 5px rgb(47, 42, 42);
  text-transform: capitalize;
  text-decoration: none;
  animation: 'fadeIn 0.5s';
  display: flex;
  justify-content: center;
}

.judul {
  display: flex;
  gap: 5px;
}

.judul p {
  --max-lines: 1;
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: var(--max-lines);
  font-family: 'poppins', sans-serif;
  max-width: 150px;
}

.first-front {
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 50px;
}

.prev {
  margin-right: 15px;

}

.next {
  margin-left: 15px;
}

.komen-btn {
  width: 0px;
  height: 30px;
  margin-top: 3px;
  margin-left: 15px;
  border-radius: 5px;
  animation: 'fadeIn 10s';
}

.close-btn {
  text-shadow: 1px 1px 5px rgb(47, 42, 42);
  display: none;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  padding-top: 3px;
  height: 25px;
  width: 25px;
  cursor: pointer;
  position: absolute;
  z-index: 3;
  transform: translate(10px, 10px);
  animation: 'fadeIn 0.5s'
}