.list {
    box-shadow: inset 0px -15px 20px -15px black;
    height: 180px;
    border-radius: 5px;
    width: 120px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    transition: 0.3s;
    cursor: pointer;
    background-position: center;
}
.galeri{
    height: 260px;
    width: 100%;
    text-align: center;
    text-justify: center;
    font-size: small;
    position: relative;
    vertical-align: bottom;
    text-shadow: 0 1px 3px black;
    color: white;
    font-family: 'Poppins', sans-serif;
    line-height: 100%;
}
.galeri div{
    font-family:'Poppins', sans-serief;
    width: inherit;
    height: 50px;
    background: rgb(47, 42, 42, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius:3px 3px 0 0;
    padding: 3px;
}
.list:hover {
    background-size: 120% 120%;
}

